/* General.js */

* {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

html {
    font-family: 'Montserrat', sans-serif;
    background-color: #465163;
}

/* App.js */

.app__container {
    width: 700px;
    height: 100vh;
    margin-left: auto;
    margin-right: auto;
    background: rgba(15, 17, 15, 0.5) ;
    color: white;
    text-align: center;
}

.app__p {
    padding-top: 5vh;
    width: 60%;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
}

/* Form.js */

.form__component {
    text-align: center;
    margin-top: 40px;
}

.form__component input {
    margin-bottom: 2rem;
}

.form__button {
    font-size: 20px; 
    margin-bottom: 3rem;
    padding: 0.5rem;
    width: 150px;
    background-color: rgb(32, 102, 160);
    color: white;
}
  
.form__button:hover {
    background-color: rgb(101, 173, 231);
}

/* Weather.js */

.weather__component {
    text-align: center;
    width: 70%;
    text-align: center;
    margin: auto;
}

.weather__temperature {
    font-size: 3rem;
    line-height: 12rem; 
}

.weather__location {
    margin-top: 8rem;
}

.weather__description {
    text-align: center;
}

/* Media */

@media only screen and (max-width: 710px ), (max-height: 600px) {
    .app__p {
        font-size: 0.6rem;
    }

    .app__container {
        width: 100vw;
        height: 100vh;
        margin-top: 0vh;
    }

    .form__button {
        margin-top: 20px;
    }

    .weather__temperature {
        font-size: 1.2rem;
        line-height: 6rem; 
    }

    .weather__location {
        margin-top: 2rem;
    }

    .app__p {
        width: 85%;
    }
}